.subContainer {
    position: relative;
    margin-top: 4rem;
}
  
  .btnwrapper{
    position: relative;
    height: fit-content;
    width: initial;
    display: flex;
    justify-content: center;
    align-items: center;
    background: white;
  }
  
  .link_wrapper{
    position: relative;
   }
  
  .btn{
    display: block;
    width: 250px;
    height: 60px;
    line-height: 50px;
    font-weight: bold;
    text-decoration: none;
    background: #060b26;
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 1px;
    border: 3px solid #57c4e5;
    transition: all .35s;
  }
  
  .btnicon{
    width: 50px;
    height: 50px;
    border: 3px solid transparent;
    position: absolute;
    transform: rotate(45deg);
    right: 0;
    top: 0;
    z-index: +1;
    transition: all .35s;
  }
  
  .btnicon svg{
    width: 30px;
    position: absolute;
    top: calc(50% - 15px);
    left: calc(50% - 15px);
    transform: rotate(-45deg);
    fill: #57c4e5;
    transition: all .35s;
  }
  
  .btn:hover{
    width: 200px;
    border: 3px solid #57c4e5;
    background: transparent;
    color: #57c4e5;
  }
  
  .btn:hover + .btnicon{
    border: 3px solid #57c4e5;
    right: -25%;
  }
  .enrollContainer{
    margin: 1rem;
    padding-bottom: 1.5rem;
    border-bottom: 1px solid grey;
  }
  .enroll{
    width: 100%;
   
  }
  
