.jobInfo {
  text-align: center;
  background: whitesmoke;
  color:#060b26;
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.jobList {
  display: grid;
  grid-template-columns: 90%;
  grid-template-rows: auto;
  row-gap: 45px;
  justify-content: center;
  align-items: center;
  padding: 20px;
  }
.vacantPositions1, .vacantPositions2, .vacantPositions3, .vacantPositions4 {
background: white;
display: grid;
grid-template-columns: 100%;
grid-template-rows: 180px 180px 60px;
grid-template-areas: "image" "text" "requirements";
border-radius: 18px;
box-shadow: 5px 5px 15px rgba(0,0,0,0.9);
font-family: Verdana, Geneva, Tahoma, sans-serif;
text-align: center;
}

.vacancyImage {
  grid-area: image;
  background: url("./img/img3.jpg");
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  background-size: cover;
}
.jobText {
  grid-area: text;
  
  text-align: center;
  padding-left: 5px;
  padding-right: 5px;
 
}
.jobRequirements {
  grid-area: requirements;
  background: #57c4e5;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.vacantPositions1:hover {
  transform: scale(1.15);
  box-shadow: 5px 5px 15px rgba(0,0,0,0.6);
}
.vacantPositions2:hover {
  transform: scale(1.15);
  box-shadow: 5px 5px 15px rgba(0,0,0,0.6);
}
.vacantPositions3:hover {
  transform: scale(1.15);
  box-shadow: 5px 5px 15px rgba(0,0,0,0.6);
}
.vacantPositions4:hover {
  transform: scale(1.15);
  box-shadow: 5px 5px 15px rgba(0,0,0,0.6);
}
.vacantPositions1, .vacantPositions2, .vacantPositions3, .vacantPositions4 {
 
  transition: 0.5s ease;
  cursor: pointer;
  margin:10px;
}

