.nav-Links {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
  list-style: none;
  outline-style: none;
  
}
.home {
  color: black;
  background: white;
  border: none;
  height: 45px;
  width: 100px;
  text-decoration: none;
  font-size: large;
  font-weight: bold;
  transition: 500ms ease;
}
.home:hover {
  transform: scale(1.15);
  color: white;
  background: #57c4e5;
  border: 2px solid white;
}
.signup {
  margin-right: 1rem;
  border: none;
  color: black;
  background: white;
  height: 45px;
  width: 100px;
  text-decoration: none;
  font-size: large;
  font-weight: bold;
  transition: 500ms ease;
}
.signup:hover {
  transform: scale(1.15);
  color: white;
  background: #57c4e5;
  border: 2px solid white;
}
@media only screen and (min-width: 600px) {
  .login {
    margin-right: 2rem;
    border: 2px solid #57c4e5;
    color: black;
    background: white;
    border-radius: 18px;
    height: 35px;
    width: 65px;
    text-decoration: none;
    transition: 500ms ease;
  }
  .login:hover {
    transform: scale(1.15);
    color: white;
    background: #57c4e5;
    border: 2px solid white;
  
  }
  .signup {
    margin-right: 2rem;
    margin-left: 2rem;
    border: 2px solid #57c4e5;
    color: black;
    background: white;
    border-radius: 18px;
    height: 35px;
    width: 65px;
    text-decoration: none;
    transition: 500ms ease;
  }
  .signup:hover {
    transform: scale(1.15);
    color: white;
    background: #57c4e5;
    border: 2px solid white;
  }
}
.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  
  }

  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: #57c4e5;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
   }
  
  span {
    margin-left: 16px;
  }